	export default {
		props:{
			content:{
				type:Object,
			
			}
		},
		data() {
			return {
				cont:"",
			    agreen:false,
				showlg:false,
				nickNames:"",
				avatarUrl:"",
				color:"#08c05f",
				phone:"",
				yzm:"",
			}
		},
		mounted(){
			this.cont=this.$props.content;
		},
		methods:{
		  changgreen(){
			  this.agreen=!this.agreen;
		  },
		  changlogin(){
			  let that=this;
			 that.showlg=true;
			  uni.login({
			    provider: 'weixin',
			    success: function(loginRes) {
			      console.log(loginRes);
			      // 获取用户信息
			      uni.getUserInfo({
			        provider: 'weixin',
			        success: function (infoRes) {
                      that._data.nickNames = infoRes.userInfo.nickName;  	 		 
					  that._data.avatarUrl= require(infoRes.userInfo.avatarUrl);
			        }
			      });
			    }
			  });
		  },
		  yclogin(){
			 this.showlg=false; 
		  },
		  submit(){
			 if(this.phone==""){
				 uni.showToast({
				     title: '请输入电话号码',
				     duration: 1000,
				 	 icon:"none"
				 });
				 return  false;
			 }
			  if(this.yzm==""){
				  uni.showToast({
				      title: '请输入验证码',
				  	  duration: 1000,
				  	  icon:"none",
				 
				  });
				   return false;	 
			  }
			  if(!this.agreen){
				  uni.showToast({
				      title: '请阅读并同意协议',
				  	  duration: 1000,
				  	  icon:"none"
				  });
				 return false; 
			  };
			  
			  
			  
		  },
		  yzmcheck(){
			   var phonecheck=/^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
			   if(!phonecheck.test(this.phone)){
				   uni.showToast({
				       title: '请输入正确的电话号码',
				   					  duration: 1000,
				   					  icon:"none"
				   });
			   }
		  },
		  phonebd(){
			  this.showlg=false;
			  uni.navigateTo({
			      url: './phone-check'
			  });
		  }
		 

		}
	}
